.single {
    margin-bottom: 20rem;
}

.single__inner {
    display: flex;
    align-items: start;
    gap: 10rem;
    margin-bottom: 5.5rem;
}

.single__img {
    width: 50rem;
    height: auto;
    max-height: 73.5rem;
    overflow: hidden;
}

.single__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.single__content {
    display: flex;
    align-items: start;
    gap: 5rem;
}

.single__content__name {
    font-size: 5.2rem;
    font-weight: 600;
    margin-bottom: 6.6rem;
}

.single__content__text {
    display: flex;
    flex-direction: column;
    gap: 2.4rem;
    font-size: 2.4rem;
    font-weight: 400;
    text-wrap: balance;
    margin-bottom: 6.7rem;
}

.single__content__text__item__title {
    font-weight: 600;
}

.single__content__btn {
    display: flex;
    align-items: center;
    gap: 3.3rem;
    font-size: 4rem;
    font-weight: 600;
    text-decoration: none;
    transition: all 0.3s ease;

}

.single__content__btn img {
    width: 6rem;
    height: 6rem;
}

@media(hover: hover) {
    .single__content__btn:hover {
        opacity: 0.8;
    }
}

.single__content__tag {
    flex-shrink: 0;
    font-size: 1.9rem;
    font-weight: 400;
    padding: 1.5rem 2.5rem;
    border: 1px solid var(--white);
    text-decoration: none;
    transition: all 0.3s ease;

}

@media (hover: hover) {
    .single__content__tag:hover {
        background-color: #222222;
    }
}

.single__images__inner {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, auto);
    gap: 6rem;
}

.single__images__item_img1 {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    height: 108.1rem;
}

.single__images__item_img2 {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    height: 40.2rem;
    width: 60.2rem;
    margin-left: auto;
}

.single__images__item_text {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
}

.single__images__item {
    overflow: hidden;
}

.single__images__item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}


/* Skills  */
.skills {
    margin-bottom: 20rem;
}

.skills__inner {
    display: flex;
    align-items: stretch;
    gap: 20rem;
    margin-bottom: 10rem;
}

.skills__left {
    flex-shrink: 0;
}

.skills__title {
    margin-bottom: 5.5rem;
}

.skills__img {
    width: 40rem;
    height: 53.5rem;
    object-fit: cover;
    overflow: hidden;
}

.skills__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.skills__list {
    display: flex;
    flex-direction: column;
    gap: 4.8rem;
    counter-reset: skills;
}

.skills__item {
    font-size: 3rem;
    font-weight: 400;
    display: flex;
    align-items: center;
    text-wrap: balance;
    margin-left: 13rem;
}

.skills__item::before {
    content: "0" counter(skills);
    counter-increment: skills;
    font-size: 7.5rem;
    font-weight: 400;
    margin-right: 7rem;
}

.skills__item:nth-child(2n) {
    margin-left: 26.3rem;
}

.skills__item:nth-child(4n) {
    margin-left: 0;
}

.skills__rate__images {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 6rem;
    margin-bottom: 5.5rem;
}

.skills__rate__images__item {
    height: 50rem;
}

.skills__rate__images__item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.skills__rate__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1rem;
}

.skills__rate__list__item {
    font-size: 3.5rem;
    text-align: center;
}

/* Projects  */
.projects__title {
    margin-bottom: 10rem;
}

.projects__list {
    display: flex;
    flex-direction: column;
    gap: 10rem;
}

.projects__item {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 6rem;
}

.projects__item__content {
    display: flex;
    align-items: center;
}

.projects__item__name {
    font-size: 4.5rem;
    text-align: left;
    margin-bottom: 3rem;
}

.projects__item__text {
    font-size: 3rem;
    text-wrap: balance;
}

.projects__item__img {
    height: 40.5rem;
    background-color: #fff;
}

.projects__item:nth-child(2n) .projects__item__img:nth-child(2) {
    order: 1;
}

.projects__item:nth-child(2n) .projects__item__img:nth-of-type(3) {
    order: 3;
}

.projects__item:nth-child(2n) .projects__item__img:nth-of-type(4) {
    order: 4;
}

.projects__item:nth-child(2n) .projects__item__content {
    order: 2;
    padding-left: 4rem;
}